.Home{
  background-color: #f4f8fd;
}
.home_head{
  height: 300px;
  background-color: #2f7ee6;
}
.home_val{
  display: flex;
  flex-direction: row;
  height: 130px;
  align-items: center;
  max-width: 1120px;
  width: 96%;
  margin: auto;
  color: #ffffff;
}
.home_val .val_logo{
  height: 64px;
  width: 64px;
  background-color: #ffffff;
}
.home_val .val_name{
  display: flex;
  flex-direction: column;
  padding-left: 20px;
}
.home_val .val_name span:nth-child(1){
  font-size: 34px;
  line-height: 34px;
}
.home_val .val_name span:nth-child(2){
  font-size: 24px;
  line-height: 24px;
}
.home_val .val_info{
  font-size: 24px;
  text-align: center;
  padding-left: 70px;
}
.flex1{
  flex: 1;
}
.val_welcome span{
  padding-right: 20px;
}
.val_welcome a{
  color: #ffffff;
}

/* home_menu */
.home_menu{
  margin: auto;
  height: 74px;
  width: 100%;
  max-width: 1200px;
  border-radius: 37px;
  display: flex;
  flex-direction: row;
  background-color: #3a495f;
}
.home_menu .menu_item{
  flex: 1;
  height: 46px;
  border-radius: 23px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 14px 12px;
  color: #ffffff;
  justify-content: center;
}
.home_menu .menu_item img{
  height: 30px;
  margin-right: 20px;
}
.menu_item:hover,.menu_item_sel{
  background: linear-gradient(#3d5974,#3d5974,#3a4b63);
}


/* home_container */
.home_container{
  width: 100%;
  max-width: 1200px;
  margin: auto;
  position: relative;
  top: -90px;
  display: flex;
  flex-direction: row;
}
.left_menu{
  width: 280px;
  background-color: #cce9ff !important;
  border-radius: 20px;
  overflow: hidden;
}
.left_menu_item{
  color: #2e7ae5;
  background-color: #cce9ff !important;
  border-bottom: 1px solid #b8d2e6;
}
.left_menu_item_name{
  color: #2e7ae5;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 20px;
}
.left_menu_item_name span{
  flex:1;
  text-align: left;
  padding-left: 20px;
}
.left_menu_item_name img{
  width: 20px;
  height: 20px;
}
.left_menu_item_name_sel{
  font-weight: bold;
}
.child_item_name{
  padding-left: 40px;
}
.ant-menu{
  background-color: #cce9ff !important;
}


.right_box{
  border-radius: 20px;
  background-color: #ffffff;
  flex: 1;
  margin-left: 10px;
  overflow: hidden;
}
.right_head{
  height: 90px;
  width: 100%;
  background-color: #f4f8fd;
  display: flex;
  flex-direction: row;
  padding: 10px 40px;
  text-align: left;
}
.right_head_left{
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
}
.right_head_left div:nth-child(1){
  color: #38a1ee;
  font-size: 20px;
  font-weight: bold;
}
.right_head_link{
  text-decoration: underline;
  margin-top: 15px;
  margin-right: 10px;
}
.right_val{
  padding: 40px;
}
.ant-table-thead{
  display: none;
}